___CSS_LOADER_EXPORT___.push([module.id, ".slide-fade-enter-active[data-v-86317376]{transition:all .3s ease}.slide-fade-leave-active[data-v-86317376]{transition:all .3s cubic-bezier(1,.5,.8,1)}.slide-fade-enter[data-v-86317376],.slide-fade-leave-to[data-v-86317376]{opacity:0;transform:translateX(10px)}", "",{"version":3,"sources":["webpack://./resources/js/admin/pages/dashboard/invoice/create.vue"],"names":[],"mappings":"AA0OA,0CACA,uBACA,CAEA,0CACA,0CACA,CAEA,yEAIA,SAAA,CADA,0BAEA","sourcesContent":["<template>\n  <transition appear mode=\"out-in\" name=\"fade\">\n    <Loader v-if=\"$root.$loading.show\" key=\"1\" />\n\n    <div v-else id=\"groupCollapse\" key=\"2\">\n      <div class=\"row justify-content-center mb-5\">\n        <div class=\"col-12 col-mg-8 col-lg justify-content-center\">\n          <Stepper :bus=\"bus\" :count=\"6\" />\n        </div>\n      </div>\n\n      <div class=\"row mx-0\">\n        <div class=\"col-12\">\n          <transition appear mode=\"out-in\" name=\"slide-fade\">\n            <Step_1 v-if=\"stepForm === 1\" key=\"1\" :companies=\"companies\" />\n            <Step_2 v-if=\"stepForm === 2\" key=\"2\" />\n            <Step_3 v-if=\"stepForm === 3\" key=\"3\" :hotels-own=\"form.hotels\" />\n            <Step_4 v-if=\"stepForm === 4\" key=\"4\" :transfer-data=\"form.transfer\" />\n            <Step_5 v-if=\"stepForm === 5\" key=\"5\" :sample=\"form.sample\" />\n            <Step_6 v-if=\"stepForm === 6\" key=\"6\" :hotline=\"form.hotline\" />\n          </transition>\n        </div>\n      </div>\n    </div>\n  </transition>\n</template>\n<script>\nimport Vue from 'vue'\nimport axios from 'axios'\nimport Stepper from '~/admin/components/Stepper.vue'\nimport Step_1 from \"~/admin/components/Steps/Step_1.vue\"\nimport Step_2 from \"~/admin/components/Steps/Step_2.vue\"\nimport Step_3 from \"~/admin/components/Steps/Step_3.vue\"\nimport Step_4 from \"~/admin/components/Steps/Step_4.vue\"\nimport Step_5 from \"~/admin/components/Steps/Step_5.vue\"\nimport Step_6 from \"~/admin/components/Steps/Step_6.vue\";\nimport Loader from \"~/admin/components/Loader.vue\"\n\nexport default {\n  name: \"Create\",\n  components: {\n    Stepper,\n    Step_1,\n    Step_2,\n    Step_3,\n    Step_4,\n    Step_5,\n    Step_6,\n    Loader\n  },\n  data: () => ({\n    stepForm: 1,\n    bus: new Vue(),\n    companies: [],\n    form: {\n      company: '',\n      user: {\n        name: '',\n        nationality: ''\n      },\n      hotels: [],\n      transfer: null,\n      sample: null,\n      hotline: {\n        phone: '',\n        cost: ''\n      }\n    }\n  }),\n  computed: {\n    step: {\n      get: function () {\n        return this.stepForm\n      },\n\n      set: function (value) {\n        console.log(\"Пришло на set\", value)\n        if (value < 0 || value > 6) {\n          this.$vs.notification({\n            duration: 2000,\n            sticky: true,\n            position: 'top-right',\n            color: 'danger',\n            title: 'Ошибка!',\n            text: 'Вы ушли за границу данных'\n          })\n        } else {\n          this.stepForm = value\n          console.log(\"Сохранило в set\", value)\n        }\n      }\n    },\n\n    companyError() {\n      if (this.form.company === '' || this.form.company === null) {\n        return this.$t('invoice.errors.company')\n      }\n\n      return false\n    },\n\n    userNameError() {\n      if (this.form.user.name === '' || this.form.user.name === null) {\n        return this.$t('invoice.errors.user.name')\n      }\n\n      return false\n    },\n    userNationalityError() {\n      if (this.form.user.nationality === '' || this.form.user.nationality === null) {\n        return this.$t('invoice.errors.user.nationality')\n      }\n\n      return false\n    }\n  },\n  async mounted() {\n\n    this.bus.$on('changeStep', this.changeStep)\n\n    await axios.get('/api/companies')\n      .then(r => {\n        this.companies = r.data.payload.companies\n      })\n      .catch(e => {\n        this.$vs.notification({\n          title: this.$t('notification.get.danger.title'),\n          text: this.$t('notification.get.danger.text')\n        })\n      })\n\n    await this.$root.$loading.finish()\n  },\n  methods: {\n    changeStep(index) {\n      console.log('changeStep', index)\n      this.step = index\n    },\n\n    notifyError() {\n      this.$vs.notification({\n        duration: 2000,\n        sticky: true,\n        position: 'top-right',\n        color: 'danger',\n        title: this.$t('invoice.errors.validateInputs.title'),\n        text: this.$t('invoice.errors.validateInputs.text')\n      })\n    },\n\n    next(callback) {\n      if (this.step === 1) {\n        if (this.companyError) {\n          this.notifyError()\n\n          return false\n        }\n        this.bus.$emit('setStatus', {\n          step: this.step,\n          status: 'success'\n        })\n      } else if (this.step === 2) {\n        if (this.userNationalityError || this.userNameError) {\n          this.notifyError()\n\n          return false\n        }\n        this.bus.$emit('setStatus', {\n          step: this.step,\n          status: 'success'\n        })\n      } else if (this.step === 3) {\n        this.bus.$emit('setStatus', {\n          step: this.step,\n          status: 'success'\n        })\n      } else if (this.step === 4) {\n        this.bus.$emit('setStatus', {\n          step: this.step,\n          status: 'success'\n        })\n      } else if (this.step === 5) {\n        this.bus.$emit('setStatus', {\n          step: this.step,\n          status: 'success'\n        })\n      } else if (this.step === 6) {\n        this.createInvoice()\n      }\n      if (typeof callback === 'function') {\n        callback()\n      }\n    },\n\n    createInvoice() {\n      this.loading = true\n      axios.post(\"/api/invoices\", {\n        ...this.form\n      })\n        .then(r => {\n          if (r.data.success) {\n            this.$router.push({name: 'dashboard.invoice.show', params: {id: r.data.payload.invoice.id}})\n          } else {\n            this.notifyError()\n          }\n        })\n        .catch(e => {\n          if (e.response.status === 422) {\n            this.$vs.notification({\n              title: 'Ошибка',\n              text: e.response.data.message\n            })\n\n            Object.keys(e.response.data.errors).forEach((key) => {\n              this.$vs.notification({\n                title: key.capitalize(),\n                text: e.response.data.errors[key]\n              })\n            })\n          } else if (e.response.status === 404) {\n            this.$vs.notification({\n              title: 'Ошибка',\n              text: e.response.data.message\n            })\n          }\n        })\n    }\n  }\n}\n</script>\n\n<style scoped>\n/* Анимации появления и исчезновения могут иметь */\n/* различные продолжительности и динамику.       */\n.slide-fade-enter-active {\n  transition: all .3s ease;\n}\n\n.slide-fade-leave-active {\n  transition: all .3s cubic-bezier(1.0, 0.5, 0.8, 1.0);\n}\n\n.slide-fade-enter, .slide-fade-leave-to\n  /* .slide-fade-leave-active до версии 2.1.8 */\n{\n  transform: translateX(10px);\n  opacity: 0;\n}\n</style>"],"sourceRoot":""}]);
